<template>
  <div class="pt-4">
  <loading :active.sync="isLoading" ></loading>
  <div class="btn-group" role="group" >
  <button type="button" class="btn btn-primary " @click="openModal(true)">
  
  </button>
  </div>
    <table  class="table table-sm mt-3">
      <thead>
      <th width="120"></th>
      <th></th>
      <th width="120"></th>
      <th width="120"></th>
      <th width="120"></th>
      <th width="180"></th>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td>{{item.originPrice | currency}}</td>
          <td>{{item.price | currency}}</td>
          <td>
            <span v-if="item.is_enabled"></span>
            <span v-else></span>
          </td>
          <td>
           <div class="btn-group" role="group" >
            <button class="btn btn-outline-primary" @click="openModal(false, item)"></button>
            <button class="btn btn-outline-danger" @click="delectProduct(item)"></button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  <Pagination :page="pagination" @goToPage="getProducts"></Pagination>
    <!-- Modal -->
<div class="modal fade" id="productModal" tabindex="-1" role="dialog"
  aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-lg" role="document">
    <div class="modal-content border-0">
      <div class="modal-header bg-dark text-white">
        <h5 class="modal-title" id="exampleModalLabel">
          <span></span>
        </h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <div class="row">
          <div class="col-sm-4">
            <div class="form-group">
              <label for="image"></label>
              <input @keyup.enter="uploadFile" type="text" class="form-control" id="image" v-model.lazy="tempProduct.imageUrl"
                placeholder="">
            </div>
            <div class="form-group">
              <label for="customFile"> 
                <i class="fas fa-spinner fa-spin"  v-if="loadspin"></i>
              </label>
              <input type="file" id="customFile" class="form-control"
                ref="files" @change="uploadFile">
            </div>
            <img v-if="tempProduct.imageUrl" :src="tempProduct.imageUrl"
              class="img-fluid" alt="tempProduct.imageUrl">
          </div>
          <div class="col-sm-8">
            <div class="form-group">
              <label for="title"></label>
              <input type="text" class="form-control" v-model="tempProduct.title" id="title"
                placeholder="">
            </div>

            <div class="form-row">
              <div class="form-group col-md-6">
                <label for="category"></label>
                <input type="text" class="form-control" id="category" v-model="tempProduct.category"
                  placeholder="">
              </div>
              <div class="form-group col-md-6">
                <label for="price"></label>
                <input type="unit" class="form-control" id="unit" v-model="tempProduct.unit "
                  placeholder="">
              </div>
            </div>

            <div class="form-row">
              <div class="form-group col-md-6">
              <label for="origin_price"></label>
                <input type="number" class="form-control" id="origin_price" v-model.number="tempProduct.originPrice"
                  placeholder="">
              </div>
              <div class="form-group col-md-6">
                <label for="price"></label>
                <input type="number" class="form-control" id="price" v-model.number="tempProduct.price "
                  placeholder="">
              </div>
            </div>
            <hr>

            <div class="form-group">
              <label for="description"></label>
              <textarea type="text" class="form-control" id="description" v-model="tempProduct.description"
                placeholder=""></textarea>
            </div>
            <div class="form-group">
              <label for="content"></label>
              <textarea type="text" class="form-control" id="content" v-model="tempProduct.content"
                placeholder=""></textarea>
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox" v-model="tempProduct.is_enabled" true-value="1" false-value="0"
                  id="is_enabled">
                <label class="form-check-label" for="is_enabled">
                  
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
        <button type="button" class="btn btn-primary" @click="updateProduct"></button>
      </div>
    </div>
  </div>
</div>

  </div>
</template>

<script>
import $ from 'jquery'
import Pagination from '../pages/Pagination.vue'

export default{
  data () {
    return {
      loadspin: false,
      isLoading: true,
      products: [],
      isNew: false,
      pagination: {},
      tempProduct: {
        imageUrl: '',
        title: '',
        is_enabled: 0,
        category: '',
        unit: '',
        originPrice: '',
        price: '',
        description: '',
        content: ''
      }
    }
  },
  components: {
    Pagination
  },
  methods: {
    getProducts (page = 1) {
      const vm = this
      vm.isLoading = true
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/products?page=${page}`
      this.axios.get(api).then((res) => {
        vm.products = res.data.products
        vm.pagination = res.data.pagination
        vm.isLoading = false
      })
    },
    openModal (isNew, item) {
      if (isNew) {
        this.tempProduct = {}
        this.isNew = true
      } else {
        // ,itemby referance, Object.assign({},item)
        this.tempProduct = Object.assign({}, item)
        this.isNew = false
      }
      $('#productModal').modal('show')
    },
    updateProduct () {
      const vm = this
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`
      let httpMethod = 'post'
      if (!vm.isNew) {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`
        httpMethod = 'put'
      }
      this.axios[httpMethod](api, { 'data': vm.tempProduct }).then((res) => {
        console.log(res.data)
        if (res.data.success) {
          $('#productModal').modal('hide')
          vm.getProducts()
        } else {
          $('#productModal').modal('hide')
          vm.getProducts()
          console.log('')
        }
      })
    },
    delectProduct (item) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${item.id}`
      this.axios.delete(api).then((res) => {
        $('#productModal').modal('hide')
        this.getProducts()
      })
    },
    uploadFile () {
      const vm = this
      const uploadedFile = this.$refs.files.files[0]
      const formData = new FormData()
      vm.loadspin = true
      formData.append('file-to-upload', uploadedFile)
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`
      this.axios.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((res) => {
        console.log(res.data)
        if (res.data.success) {
          console.log('upload')
          vm.$set(vm.tempProduct, 'imageUrl', res.data.imageUrl)
          vm.loadspin = false
        } else {
          this.$bus.$emit('message:push', res.data.message, 'danger')
        }
      })
    }
  },
  created () {
    this.getProducts()
  }
}
</script>
